import { Element } from "react-scroll";
import img from '../images/time-table-2.jpg'
import logo from '../images/main-logo.png'
import header from '../images/time-table-header.jpg'
import { useInView } from "react-intersection-observer";
import styled from "styled-components";
import { Card, Col, Container, Row  } from "react-bootstrap";
import { Icon } from '@iconify/react';
import { useIsMobile } from "../hooks/useIsMobile";
const Section = styled.section`
  min-height: 100vh;
  overflow: hidden;
  padding: var(--nav-height) 0;
  opacity: 0;
  transition: 1.5s all ease-in-out;
  white-space: pre-line;

  &.in {
    opacity: 1;
  }


  .card {
    border: none;
    border-radius: 10px;
    height: 100%;
  }

  .c-details span {
      font-weight: 300;
      font-size: 0.8rem;
  }

.icon {
    width: 50px;
    height: 50px;
    background-color: #eee;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem
}

.badge span {
    background-color: #fffbec;
    width: 60px;
    height: 25px;
    padding-bottom: 3px;
    border-radius: 5px;
    display: flex;
    color: #fed85d;
    justify-content: center;
    align-items: center
}


.description {
    font-size: 0.9rem;
    font-weight: 600
    color: #a5aec0
}

.shake {
  animation: shake 5s ease infinite;
}
@keyframes shake {
	0%, 100% {transform: translateX(0);}
	10%, 30%, 50%, 70%, 90% {transform: translateX(-5px);}
	20%, 40%, 60%, 80% {transform: translateX(5px);}
}
`;

export default function Programs() {
  const {ref, inView} = useInView({root:null, rootMargin: "0px", threshold: 0});
  const isMobile = useIsMobile();

  const dbSpeakers = [
    {icon: 'bx:bxl-mailchimp', category: 'DB/SQL', name: '', time: '[] 15:10 ~ 15:40', title: 'jOOQ, SQL   ', subTitle: '', description: ' SQL       jOOQ   .'},
    {icon: 'bx:bxl-mailchimp', category: 'DB/SQL', name: '', time: '[] 15:50 ~ 16:20', title: 'JPA Patterns', subTitle: '', description: '  JPA       .'},
    {icon: 'bx:bxl-mailchimp', category: 'DB/SQL', name: '', time: '[] 14:30 ~ 15:00', title: ' :  ', subTitle: '(    )', description: '          .'},
  ]
  const performanceSpeakrs = [
    {icon: 'bx:bxl-mailchimp', category: '', name: '', time: '[] 16:50 ~ 17:20', title: 'Kotlin Injection', subTitle: '(      )', description: ''},
    {icon: 'bx:bxl-mailchimp', category: '', name: '', time: '[] 17:30 ~ 18:00', title: 'JDK Structured Concurrency', subTitle: '', description: 'JDK21  Preview  Structured Concurrency  .'},
    {icon: 'bx:bxl-mailchimp', category: '', name: '', time: '[] 15:10 ~ 15:40', title: '  API  ,  ', subTitle: '', description: ''},
    {icon: 'bx:bxl-mailchimp', category: '', name: '', time: '[] 15:50 ~ 16:20', title: 'gRPC ', subTitle: '', description: ''},
  ]
  const speakers = [
    {icon: 'bx:bxl-mailchimp', category: '', name: '', time: '[] 17:30 ~ 18:00', title: '2     ', subTitle: '', description: ''},
    {icon: 'bx:bxl-mailchimp', category: '', name: '', time: '[] 16:50 ~ 17:20', title: '  ', subTitle: '(   )', description: ''},
    {icon: 'bx:bxl-mailchimp', category: '', name: '', time: '[] 14:30 ~ 15:00', title: '    ', subTitle: '', description: '       .'},
  ];

  return (
    <Element name={"Programs"} id="programs">
      <Section className={`${inView ? 'in' : ''}`} ref={ref}>
      <Container className="mb-3">
          <div className="mb-5" role="button" onClick={() => window.open(img, '_blank', 'noopener')}>
            <img src={header} className="w-100"/>           
          </div>
        <Row>
        <Col md={1} className="d-flex flex-col justify-content-center align-items-center text-center ">
          <h4><b>DB/SQL</b><br/> {isMobile ? '':<Icon className="shake" icon="maki:arrow" />}</h4>
        </Col>
        {
          dbSpeakers.map((speaker) => (
            <Col className="mb-3">
                <Card className="p-3">
                  <div className="d-flex justify-content-between">
                    <div className="d-flex flex-row align-items-center">
                      <div className="icon"><Icon icon={speaker.icon} /></div>
                      <div className="ms-2 c-details">
                        <h6 className="mb-0">{speaker.name}</h6>
                        <span>{speaker.time}</span>
                      </div>
                    </div>
                    <div className="badge"><span>{speaker.category}</span></div>
                  </div>
                  <div className="mt-5">
                    <h3>{speaker.title}</h3>
                    <h6>{speaker.subTitle}</h6>
                    <div className="mt-5">
                      <div className="mt-3">
                        <span className="description">{speaker.description}</span>
                      </div>
                    </div>
                  </div>
                </Card>
              </Col>
              ))
            }
      </Row>
      <Row>
      <Col md={1} className="d-flex flex-col justify-content-center align-items-center text-center">
      <h4><b></b><br/> {isMobile ? '':<Icon className="shake" icon="maki:arrow" />}</h4>
        </Col>
        {
          performanceSpeakrs.map((speaker) => (
            <Col className="mb-3">
                <Card className="p-3">
                  <div className="d-flex justify-content-between">
                    <div className="d-flex flex-row align-items-center">
                      <div className="icon"><Icon icon={speaker.icon} /></div>
                      <div className="ms-2 c-details">
                        <h6 className="mb-0">{speaker.name}</h6>
                        <span>{speaker.time}</span>
                      </div>
                    </div>
                    <div className="badge"><span>{speaker.category}</span></div>
                  </div>
                  <div className="mt-5">
                    <h3>{speaker.title}</h3>
                    <h6>{speaker.subTitle}</h6>
                    <div className="mt-5">
                      <div className="mt-3">
                        <span className="description">{speaker.description}</span>
                      </div>
                    </div>
                  </div>
                </Card>
              </Col>
              ))
            }
      </Row>
      <Row>
      <Col md={1} className="d-flex flex-col justify-content-center align-items-center text-center">
      <h4><b></b><br/> {isMobile ? '':<Icon className="shake" icon="maki:arrow" />}</h4>
        </Col>
        {
          speakers.map((speaker) => (
            <Col  className="mb-3">
                <Card className="p-3">
                  <div className="d-flex justify-content-between">
                    <div className="d-flex flex-row align-items-center">
                      <div className="icon"><Icon icon={speaker.icon} /></div>
                      <div className="ms-2 c-details">
                        <h6 className="mb-0">{speaker.name}</h6>
                        <span>{speaker.time}</span>
                      </div>
                    </div>
                    <div className="badge"><span>{speaker.category}</span></div>
                  </div>
                  <div className="mt-5">
                    <h3>{speaker.title}</h3>
                    <h6>{speaker.subTitle}</h6>
                    <div className="mt-5">
                      <div className="mt-3">
                        <span className="description">{speaker.description}</span>
                      </div>
                    </div>
                  </div>
                </Card>
              </Col>
              ))
            }
      </Row>
    </Container>
    {/*        <Col md={3} className="mb-3">
        <Card className="p-3 justify-content-center align-items-center gap-5" role="button" onClick={() => window.open(img, '_blank', 'noopener')}>
          <img src={logo} className="w-50" />
          <span> </span>
          </Card>
       </Col>
 */}
        {/* <img src={img} style={{cursor: 'pointer'}} onClick={() => window.open(img, '_blank', 'noopener')}/> */}
      </Section>
    </Element>
  );
}
