import React from "react";
import { Accordion } from "react-bootstrap";
import { useInView } from "react-intersection-observer";
import { Element } from "react-scroll";
import styled from "styled-components";


const Section = styled.section`
opacity: 0;
transition: 1s all ease-in-out;
padding: calc(var(--nav-height) + 20px) 0;
white-space: pre-line;

&.in {
  opacity: 1;
}

  min-height: 100vh;
  max-width: 60vw;
  margin: 0 auto;
  flex-basis: content;
  
  p { margin: 0}
  .title {
    font-size: 4rem;
    text-align: center;
    font-weight: 600;
    margin-bottom: 3vh;
  }

  .accordion-button {
    font-size: 1rem;
  }
  @media (max-width: 768px) {
    max-width: 90vw;
    .title {
      font-size: 3rem;
      margin-bottom: 2vh;
    }
  }

`

export default function Sponsors() {
  const {ref, inView} = useInView({root:null, rootMargin: "0px", threshold: 0});
  const faqs = [
    {header: ' /  ?', body: `  festa.io  .      https://festa.io/explore  . \n   https://festa.io/my/tickets    .`},
    {header: '    ?', body: ' .    .'},
    {header: '   ?', body: '      .'},
    {header: '     ?', body: '       .   .'},
    {header: '   .', body: 'support@lazyconf.dev   .       .'},
  ]

  const cocs = [
    {header: '  .', body: '      ,     . \n   ,   .    .'},
    {header: ' .', body: '         . \n          .'},
    {header: '  .', body: '     staff   . \n          staff  .'},
  ]


  return (
    <Element name={"Faqs"} id="faqs">
       <Section className={`d-flex flex-column gap-5 ${inView ? 'in' : ''}`} ref={ref}>
        <div className="mb-5">
          <p className="title">FAQ</p>
          <Accordion>
            {faqs.map((faq, index) => (
              <Accordion.Item eventKey={index}>
                <Accordion.Header>{faq.header}</Accordion.Header>
                <Accordion.Body>
                  {faq.body}
                </Accordion.Body>
              </Accordion.Item>
            ))}
          </Accordion>
          </div>

          <div>
          <p className="title">CoC</p>
          <Accordion>
          {cocs.map((coc, index) => (
              <Accordion.Item eventKey={index}>
                <Accordion.Header>{coc.header}</Accordion.Header>
                <Accordion.Body>
                  {coc.body}
                </Accordion.Body>
              </Accordion.Item>
            ))}
          </Accordion>
          </div>
      </Section>
    </Element>
  );
}
